<template>
  <div>
    <div
      v-for="animation in AnimationList"
      :key="animation.index"
      class="animation-container"
    >
      <a :name="animation.en_name">
        <b-card
          class="mb-3 animation-card"
          header-bg-variant="primary"
          header-text-variant="white"
          :title="' No. ' + animation.index + ' ' + animation.en_name"
          :img-src="
            './assets/animations/animation-examples/' +
            animation.en_name +
            (overAnimation == animation.en_name ? '.gif' : '.jpg')
          "
          img-alt="./assets/image/fail_loading.svg"
          img-left
          @mouseover="changeSource(animation.en_name)"
          @mouseout="changeSource(animation.en_name)"
        >
          <b-card-text class="animation-disc">
            <div class="ani-disc-box">
              <div>
                <span class="ani-sub-title"> HOW </span> <br />
                <span class="ani-text"> {{ animation.en_how }} </span>
              </div>
              <div>
                <span class="ani-sub-title"> Why </span> <br />
                <span class="ani-text"> {{ animation.en_why }} </span>
              </div>
            </div>
            <span
              v-for="idx in Array.from({length:animation.example_num},(v,k)=>k)"
              :key="'example' + idx"
            >
              <b-button
                pill
                class="example_btn"
                variant="outline-primary"
                @click="$bvModal.show('modal' + animation.en_name + idx)"
              >
                Example {{ idx + 1 }}
              </b-button>

              <b-modal :id="'modal' + animation.en_name + idx" title="Example">
                <img
                  :src="
                    './assets/animations/animation-real-examples/' +
                    animation.en_name +
                    '_' +
                    idx +
                    '.gif'
                  "
                  class="example-img"
                />
              </b-modal>
            </span>
          </b-card-text>
        </b-card>
      </a>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "AnimationGallery",
  data: function () {
    return {
      cards: Array,
      activeNRCardsGroups: Object,
      overAnimation: String,
    };
  },
  mounted: function () {
    this.overAnimation = "";
  },
  computed: {
    ...mapState({
      AnimationList: (state) => state.designSpace.animationLists,
    }),
  },
  methods: {
    changeSource: function (animationName) {
      if (this.overAnimation === animationName) {
        this.overAnimation = "";
      } else {
        this.overAnimation = animationName;
      }
    },
  },
};
</script>

