<template>
  <div>
    <div
      v-for="(nrCardsGroup, NRName) in activeNRCardsGroups"
      :key="NRName"
      :id="NRName"
      :class="NRName"
    >
      <a :name="NRName"></a>
      <div v-show="!NRFilter[NRName]" class="display-reminder">
        <div class="reminder-content">
          <div>
            <span
              class="scrollSpy-btn-symbol"
              :style="{
                backgroundColor: NarrativeRelationships[NRName].NR_color,
              }"
            ></span>
            <span class="reminder-name">
              <b>{{ NarrativeRelationships[NRName].NR_tag }}</b>
            </span>
          </div>
          <div v-if="NRName != 'Universal'" class="nr_descriptions">
            <p>
              <span
                class="nr-desc-key"
                :style="{
                  color: NarrativeRelationships[NRName].NR_color,
                }"
                ><b>Definition</b></span
              >
              {{ NarrativeRelationships[NRName].NR_desc }}
            </p>
            <p>
              <span
                class="nr-desc-key"
                :style="{
                  color: NarrativeRelationships[NRName].NR_color,
                }"
                ><b>Connecting words in natural language</b></span
              >
              {{
                '"' +
                NarrativeRelationships[NRName].NR_indicator.join('", "') +
                '"'
              }}
            </p>
            <p>
              <span
                class="nr-desc-key"
                :style="{
                  color: NarrativeRelationships[NRName].NR_color,
                }"
                ><b>Examples</b></span
              >
              {{
                NarrativeRelationships[NRName].NR_example.before +
                " -> " +
                NarrativeRelationships[NRName].NR_example.after
              }}
            </p>
          </div>
          <div class="applicable-data-changes">
            <span
              class="nr-desc-key"
              :style="{
                color: NarrativeRelationships[NRName].NR_color,
              }"
              ><b
                >Data changes that can support <i>{{ NarrativeRelationships[NRName].NR_tag }}</i
                ></b
              ></span
            >
            <span
              v-for="datachanges in NarrativeRelationships[NRName]
                .dataChanges"
              :key="datachanges"
              class="narrative-relation-data-change"
              @click="scrollToElement(NRName + '-' + datachanges, {behavior: 'smooth', block: 'center'})"
            >
              {{ datachanges }}
            </span>
          </div>
        </div>
      </div>
      <div
        v-for="(datachange, count) in NarrativeRelationships[NRName]
                .dataChanges"
        :key="datachange"
        class="data-change-cards"
      >
        <span class="data-change-subtitle">
          {{datachange}}
        </span>
        <div class="card-deck">
          <a :class="NRName + '-' + datachange"></a>
          <Card
            class="transition-card"
            v-for="cardID in NarrativeRelationships[NRName].animationCards[count]"
            :key="'card'+cardID"
            :card="TransitionCards[cardID-1]"
            :how="AnimationList[TransitionCards[cardID-1].ani_idx-1].en_how"
            why="why"
            :nrColor="NarrativeRelationships[NRName].NR_color"
            :showDOTag="true"
            :narrativeRelation="NRName"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { fetchTransitionCards } from "../api";
import Card from "./Card.vue";
import { mapState } from "vuex";

export default {
  name: "DesignSpaceGallery",
  components: {
    Card,
  },
  data: function () {
    return {
      cards: Array,
      activeNRCardsGroups: Object,
    };
  },
  computed: {
    ...mapState({
      NRCollection: (state) => state.designSpace.narrativeRelationships,
      DOCollection: (state) => state.designSpace.ataOperations,
      ELCollection: (state) => state.designSpace.editorialLayers,
      TransitionEffects: (state) => state.designSpace.transitionEffects,
      TransitionCards: (state) => state.designSpace.transitionCards,
      AnimationList: (state) => state.designSpace.animationLists,
      NRFilter: (state) => state.filter.NRFilter,
      DOFilter: (state) => state.filter.DOFilter,
      NRFilterChangeNotifier: (state) => state.filter.NRFilterChangeNotifier,
      DOFilterChangeNotifier: (state) => state.filter.DOFilterChangeNotifier,
    }),
    TransitionEffectDict: function () {
      const dict = {};
      this.TransitionEffects.forEach((effect) => {
        dict[effect.TE_tag] = {
          how: effect.how,
          why: effect.why,
        };
      });
      return dict;
    },
    NarrativeRelationships: function () {
      if (this.NRCollection == undefined || this.NRCollection[0] == undefined) {
        return {};
      }
      const NRs = {};
      this.NRCollection.forEach((NR) => {
        NRs[NR.NR_tag] = NR;
      });

      return NRs;
    },
    NRCardsGroups: function () {
      if (this.cards == undefined || this.cards[0] == undefined) {
        return {};
      }
      const cardsGroups = {};

      for (let index = 0; index < this.cards.length; index++) {
        const card = this.cards[index];
        const tag = card.NR_tag;
        if (!(tag in cardsGroups)) {
          cardsGroups[tag] = [];
        }

        const nrGroup = cardsGroups[tag];
        nrGroup.push(card);
      }

      return cardsGroups;
    },
  },
  methods: {
    scrollToElement(name, options) {
      const el = this.$el.getElementsByClassName(name)[0];
      
      if (el) {
        el.scrollIntoView(options);
      }
    }
  },
  watch: {
    NRCardsGroups: {
      handler(val) {
        this.activeNRCardsGroups = {};
        for (const key in val) {
          if (Object.hasOwnProperty.call(val, key) && !this.NRFilter[key]) {
            const element = val[key];
            this.activeNRCardsGroups[key] = element;
          }
        }
      },
      deep: true,
    },
    NRFilterChangeNotifier: function () {
      this.activeNRCardsGroups = {};
      for (const key in this.NRCardsGroups) {
        if (!this.NRFilter[key]) {
          const element = this.NRCardsGroups[key];
          this.activeNRCardsGroups[key] = element;
        }
      }
    },
  },
  beforeMount: async function () {
    this.cards = await fetchTransitionCards();
  },
};
</script>

